import React, { createContext, useState, useRef } from 'react';

import { firestore } from '../../firebase';

const StatisticsContext = createContext();


const StatisticsContextProvider = ({ children }) => {

  const [users, setUsers] = useState(['jokenox', 'giotails', 'felix1110', 'pegdro12', 'kikilingi', 'pacoloco', 'fitasq', 'furron', 'pequitas33', 'kikos9', 'pepes2', 'warronon', 'birdiela', 'warrior98', 'saokopap', 'pollitashola', 'jaz1109', 'furrita12', 'kklon90', 'chalongos'])

  const getUsers = () => {
    return users;
  };

  // ╔══════════════════════════════════════════════════════════╗
  // ║                    Statistics Database                   ║
  // ╚══════════════════════════════════════════════════════════╝

  const statisticsDB = firestore.collection('statistics');
  
  const getRanking = async (id) => {
    return await statisticsDB.doc('speaking-level').get();
  };


  return (
    <StatisticsContext.Provider value={{
      getUsers,
      getRanking
    }}>
      { children }
    </StatisticsContext.Provider>
  );


};

export { StatisticsContext, StatisticsContextProvider };