const validationsForms = (form, page) => {
  let errors = {};
  let regexName = /^[A-Za-zÑñÁáÉéÍíÓóÚúÜü\s]+$/;
  let regexUsername = /^.{5,12}$/;
  let regexEmail = /^(\w+[/./-]?){1,}@[a-z]+[/.]\w{2,}$/;
  let regexPassword = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/;
  let regexDate = /^\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])$/

  if (!form.name.trim()) {
    errors.name = "";
  }else if (!regexName.test(form.name.trim())){
    errors.name = "Only letters and blank spaces are accepted";
  }

  if (!form.lastname.trim()) {
    errors.lastname = "";
  }else if (!regexName.test(form.lastname.trim())){
    errors.lastname = "Only letters and blank spaces are accepted";
  }

  if (!form.username.trim()) {
    errors.username = "";
  }else if (!regexUsername.test(form.username.trim())){
    errors.username = "The username must have between 5 and 12 characters";
  }

  if (!form.email.trim()) {
    errors.email = "";
  }else if (!regexEmail.test(form.email.trim())){
    errors.email = "Enter a email";
  }

  if (!form.password.trim()) {
    errors.password = "";
  }else if (!regexPassword.test(form.password.trim())){
    errors.password = "The password must have between 8 and 16 characters, at least one digit, at least one lower case and at least one upper case.";
  }

  if(form.password !== form.confirmPassword){
    errors.confirmPassword = "The passwords should match each other"
  }

  if(page === 2)
  {
    if(form.esperanto_level === ""){
      errors.esperanto_level = "This field is required"
    }
  
    if(!form.birthday.trim()) {
      errors.birthday = "";
    }else if (!regexDate.test(form.birthday.trim())){
      errors.birthday = "Enter a valid date in formta yyyy-mm-dd";
    }
  
    if(form.gender === ""){
      errors.gender = "This field is required"
    }
  
    if(form.nationality === ""){
      errors.nationality = "This field is required"
    }
  }


  return errors;
};

